.third-party-management-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.tabs-header {
  display: flex;
  gap: 0.5rem;
  padding: 1.5rem 2rem 0;
  background: #f8f9fa;
  border-bottom: 2px solid #e0e0e0;
}

.tab-button {
  padding: 0.875rem 1.75rem;
  border: none;
  background: transparent;
  color: #7f8c8d;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  border-radius: 8px 8px 0 0;
  transition: all 0.3s ease;
  position: relative;
  border-bottom: 3px solid transparent;
}

.tab-button:hover {
  background: rgba(102, 126, 234, 0.1);
  color: #667eea;
}

.tab-button.active {
  background: white;
  color: #667eea;
  border-bottom: 3px solid #667eea;
}

.tab-content {
  flex: 1;
  overflow: auto;
  background: white;
}

/* Responsive */
@media (max-width: 768px) {
  .tabs-header {
    padding: 1rem;
    gap: 0.25rem;
  }

  .tab-button {
    padding: 0.75rem 1rem;
    font-size: 0.9rem;
  }
}
