/* ============================================
   SUPPLIERS - Layout Principal
   ============================================ */
.suppliers {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 1rem;
}

/* ============================================
   FILTER SECTION
   ============================================ */
.filter-section {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 20px;
  margin-top: 20px;
  padding: 15px;
  background: #f8f9fa;
  border-radius: 8px;
}

.filter-select {
  padding: 12px 16px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 1rem;
  background: white;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 200px;
}

.filter-select:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.1);
}

.search-input {
  flex: 1;
  padding: 12px 16px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.search-input:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.1);
}

.results-count {
  color: #6c757d;
  font-size: 0.95rem;
  font-weight: 500;
  white-space: nowrap;
}

/* ============================================
   TABLA DE PROVEEDORES
   ============================================ */
.suppliers-list {
  margin-top: 2rem;
}

.suppliers-table {
  width: 100%;
  border-collapse: collapse;
  background: white;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.suppliers-table thead {
  background: linear-gradient(135deg, var(--corporate-blue-700) 0%, var(--corporate-blue-800) 100%);
  color: white;
}

.suppliers-table thead th {
  padding: 1rem;
  text-align: left;
  font-weight: 700;
  font-size: 0.875rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.suppliers-table tbody tr {
  border-bottom: 1px solid var(--gray-200);
  transition: background-color 0.2s;
}

.suppliers-table tbody tr:hover {
  background-color: var(--gray-50);
}

.suppliers-table tbody tr.inactive {
  opacity: 0.6;
}

.suppliers-table tbody td {
  padding: 1rem;
  font-size: 0.9375rem;
  color: var(--gray-800);
}

/* ============================================
   CELDAS ESPECIALIZADAS
   ============================================ */
.supplier-name {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.supplier-name strong {
  color: var(--corporate-blue-800);
  font-size: 1rem;
}

.supplier-name small {
  color: var(--gray-600);
  font-size: 0.875rem;
}

.representative-info {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.representative-info div {
  color: var(--gray-800);
  font-weight: 600;
}

.representative-info small {
  color: var(--gray-600);
  font-size: 0.8125rem;
}

.contact-info {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.contact-info div {
  color: var(--gray-800);
}

.contact-info small {
  color: var(--gray-600);
  font-size: 0.8125rem;
}

/* ============================================
   STATUS BADGE
   ============================================ */
.status-badge {
  display: inline-block;
  padding: 0.375rem 0.75rem;
  border-radius: 20px;
  font-size: 0.8125rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.status-badge.active {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  color: white;
  box-shadow: 0 2px 6px rgba(16, 185, 129, 0.3);
}

.status-badge.pending {
  background: linear-gradient(135deg, #f59e0b 0%, #d97706 100%);
  color: white;
  box-shadow: 0 2px 6px rgba(245, 158, 11, 0.3);
}

.status-badge.inactive {
  background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
  color: white;
  box-shadow: 0 2px 6px rgba(239, 68, 68, 0.3);
}

/* ============================================
   ACCIONES
   ============================================ */
.supplier-actions {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.supplier-actions button {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 6px;
  font-size: 0.8125rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  white-space: nowrap;
}

.btn-secondary {
  background: linear-gradient(135deg, #6c757d 0%, #5a6268 100%);
  color: white;
}

.btn-secondary:hover {
  background: linear-gradient(135deg, #5a6268 0%, #495057 100%);
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(108, 117, 125, 0.3);
}

.btn-success {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  color: white;
}

.btn-success:hover {
  background: linear-gradient(135deg, #059669 0%, #047857 100%);
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(16, 185, 129, 0.3);
}

.btn-warning {
  background: linear-gradient(135deg, #f59e0b 0%, #d97706 100%);
  color: white;
}

.btn-warning:hover {
  background: linear-gradient(135deg, #d97706 0%, #b45309 100%);
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(245, 158, 11, 0.3);
}

.btn-danger {
  background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
  color: white;
}

.btn-danger:hover {
  background: linear-gradient(135deg, #dc2626 0%, #b91c1c 100%);
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(239, 68, 68, 0.3);
}

/* ============================================
   MODAL STYLES
   ============================================ */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2000;
  backdrop-filter: blur(4px);
}

.modal {
  background: white;
  padding: 2rem;
  border-radius: 16px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  max-width: 600px;
  width: 90%;
  max-height: 90vh;
  overflow-y: auto;
  animation: modalSlideIn 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-large {
  max-width: 900px;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-50px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.modal h3 {
  margin-top: 0;
  margin-bottom: 1.5rem;
  color: var(--corporate-blue-800);
  font-size: 1.5rem;
  font-weight: 800;
  border-bottom: 3px solid var(--corporate-blue-200);
  padding-bottom: 0.75rem;
}

/* ============================================
   MODAL CONTENT
   ============================================ */
.modal-content {
  max-height: calc(90vh - 200px);
  overflow-y: auto;
  padding-right: 0.5rem;
}

.form-section {
  margin-bottom: 2rem;
  padding: 1.5rem;
  background: var(--gray-50);
  border-radius: 12px;
  border: 1px solid var(--gray-200);
}

.form-section h4 {
  margin-top: 0;
  margin-bottom: 1rem;
  color: var(--corporate-blue-700);
  font-size: 1.125rem;
  font-weight: 700;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.form-section h4::before {
  content: '';
  width: 4px;
  height: 1.5rem;
  background: linear-gradient(135deg, var(--corporate-blue-600) 0%, var(--corporate-blue-800) 100%);
  border-radius: 2px;
}

/* ============================================
   FORM GROUPS
   ============================================ */
.form-group {
  margin-bottom: 1rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 600;
  color: var(--gray-700);
  font-size: 0.9375rem;
}

.form-group input,
.form-group select,
.form-group textarea {
  width: 100%;
  padding: 0.75rem;
  border: 2px solid var(--gray-300);
  border-radius: 8px;
  font-size: 1rem;
  box-sizing: border-box;
  transition: all 0.2s;
  background-color: white;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--corporate-blue-500);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

/* ============================================
   MODAL ACTIONS
   ============================================ */
.modal-actions {
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
  margin-top: 2rem;
  padding-top: 1.5rem;
  border-top: 2px solid var(--gray-200);
}

.modal-actions button {
  padding: 0.875rem 2rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 700;
  transition: all 0.2s;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.modal-actions .btn-secondary {
  background: linear-gradient(135deg, #6c757d 0%, #5a6268 100%);
  color: white;
  box-shadow: 0 4px 12px rgba(108, 117, 125, 0.3);
}

.modal-actions .btn-secondary:hover {
  background: linear-gradient(135deg, #5a6268 0%, #495057 100%);
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(108, 117, 125, 0.4);
}

.modal-actions .btn-primary {
  background: linear-gradient(135deg, var(--corporate-blue-600) 0%, var(--corporate-blue-700) 100%);
  color: white;
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
}

.modal-actions .btn-primary:hover {
  background: linear-gradient(135deg, var(--corporate-blue-700) 0%, var(--corporate-blue-800) 100%);
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(59, 130, 246, 0.4);
}

.modal-actions .btn-primary:disabled {
  background: #6c757d;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
  opacity: 0.6;
}

/* ============================================
   EMPTY STATE
   ============================================ */
.empty-state {
  text-align: center;
  padding: 4rem 2rem;
  background: white;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.empty-state h3 {
  color: var(--gray-700);
  margin-bottom: 0.5rem;
  font-size: 1.5rem;
}

.empty-state p {
  color: var(--gray-600);
  font-size: 1rem;
}

/* ============================================
   RESPONSIVE
   ============================================ */
@media (max-width: 1200px) {
  .suppliers-table {
    font-size: 0.875rem;
  }

  .suppliers-table thead th,
  .suppliers-table tbody td {
    padding: 0.75rem;
  }
}

@media (max-width: 768px) {
  .suppliers {
    padding: 0 0.5rem;
  }

  .suppliers-table {
    display: block;
    overflow-x: auto;
    white-space: nowrap;
  }

  .supplier-actions {
    flex-direction: column;
  }

  .supplier-actions button {
    width: 100%;
  }

  .form-row {
    grid-template-columns: 1fr;
  }

  .modal {
    width: 95%;
    padding: 1.5rem;
  }

  .modal-large {
    max-width: 95%;
  }
}

@media (max-width: 480px) {
  .modal-actions {
    flex-direction: column;
  }

  .modal-actions button {
    width: 100%;
  }
}
